body,html{height:100vh;width:100vw;margin:0;padding:0;position:relative}div{margin:0;padding:0}.menu{position:absolute;z-index:1}.soccerHealth{position:absolute;z-index:1;color:white;border:2px white solid;padding:4px;top:25px;left:25px;border-radius:4px}.footballHealth{position:absolute;z-index:1;color:white;border:2px white solid;padding:4px;top:25px;left:200px;border-radius:4px}.instructions{pointer-events:none;top:66%;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;position:absolute;left:0;right:0;z-index:1;margin-left:auto;margin-right:auto}.arrow-keys{pointer-events:none;width:150px;height:150px;opacity:0.5}#mute{top:5%;left:90%;color:white;font-size:30px;cursor:pointer}#pause{top:10%;left:90%;color:white;font-size:30px;cursor:pointer}
